/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/no-unused-vars */
// app/organizer/OrganizerClientLayout.tsx
'use client';

import React, { useEffect, useState } from 'react';
import { useTheme, useThemeClasses } from '@/hooks/useTheme';
import { useAuth } from '@/hooks/useAuth';
import { useI18n } from '@/hooks/useSafeI18n'; // Use the same hook as your dashboard
import { useRouter } from 'next/navigation';
import { SUPPORTED_LANGUAGES } from '@/hooks/useSafeI18n';
import {
    Calendar,
    Users,
    DollarSign,
    BarChart3,
    Settings,
    Plus,
    Menu,
    X,
    Home,
    MapPin,
    Ticket,
    LogOut,
    Languages
} from 'lucide-react';

interface OrganizerClientLayoutProps {
    children: React.ReactNode;
}

const OrganizerClientLayout: React.FC<OrganizerClientLayoutProps> = ({ children }) => {
    const { user, logout, isOrganizer, isLoading } = useAuth();
    const router = useRouter();
    const [sidebarOpen, setSidebarOpen] = useState(false);
    const [languageDropdownOpen, setLanguageDropdownOpen] = useState(false);

    // Theme hooks - removed initializeTheme as it doesn't exist in current implementation
    const { isDark, isCompact } = useTheme();
    const themeClasses = useThemeClasses();

    // Use the same I18n hook as dashboard and events page
    const { t, changeLanguage, currentLanguage, supportedLanguages } = useI18n();

    // Handle authentication and authorization
    useEffect(() => {
        if (!isLoading) {
            if (!user) {
                router.push('/login');
                return;
            }

            if (!isOrganizer) {
                router.push('/');
                return;
            }
        }
    }, [user, isOrganizer, isLoading, router]);

    const navigation = [
        {
            name: t('dashboard'),
            href: '/organizer/dashboard',
            icon: Home,
        },
        {
            name: t('events'),
            href: '/organizer/events',
            icon: Calendar,
        },
        {
            name: t('createEvent'),
            href: '/organizer/events/create',
            icon: Plus,
        },
        {
            name: 'Venues',
            href: '/organizer/venues',
            icon: MapPin,
        },
        {
            name: 'Ticket Management',
            href: '/organizer/tickets',
            icon: Ticket,
        },
        {
            name: 'Analytics',
            href: '/organizer/analytics',
            icon: BarChart3,
        },
        {
            name: t('settings'),
            href: '/organizer/settings',
            icon: Settings,
        }
    ];

    const handleLogout = () => {
        logout();
        router.push('/login');
    };

    const handleLanguageChange = (langCode: string) => {
        changeLanguage(langCode);
        setLanguageDropdownOpen(false);
    };

    // Show loading state with theme support
    if (isLoading) {
        return (
            <div className={`min-h-screen ${themeClasses.themeBg} flex items-center justify-center theme-transition`}>
                <div className="text-center">
                    <div className="animate-spin rounded-full h-12 w-12 border-b-2 accent-border mx-auto"></div>
                    <p className={`mt-4 ${themeClasses.themeMutedFg}`}>{t('loading')}</p>
                </div>
            </div>
        );
    }

    // Don't render anything if user is not authenticated or not an organizer
    if (!user || !isOrganizer) {
        return null;
    }

    return (
        <div className={`h-screen flex overflow-hidden ${themeClasses.themeBg} theme-transition`}>
            {/* Mobile sidebar overlay */}
            {sidebarOpen && (
                <div className="fixed inset-0 z-40 lg:hidden">
                    <div
                        className="fixed inset-0 bg-gray-600 bg-opacity-75"
                        onClick={() => setSidebarOpen(false)}
                    />
                </div>
            )}

            {/* Sidebar with theme support */}
            <div className={`fixed inset-y-0 left-0 z-50 w-64 ${themeClasses.themeCard} shadow-lg transform ${sidebarOpen ? 'translate-x-0' : '-translate-x-full'
                } transition-transform duration-300 ease-in-out lg:translate-x-0 lg:static lg:inset-0 flex flex-col theme-transition`}>

                {/* Sidebar header with theme support */}
                <div className={`flex items-center justify-between h-16 px-6 border-b ${themeClasses.themeBorder} flex-shrink-0`}>
                    <div className="flex items-center">
                        <div className="flex-shrink-0">
                            <Calendar className="h-8 w-8 accent-text" />
                        </div>
                        <div className="ml-3">
                            <h1 className={`${themeClasses.textLg} font-semibold ${themeClasses.themeFg}`}>EventPro</h1>
                            <p className={`text-xs ${themeClasses.themeMutedFg}`}>Organizer</p>
                        </div>
                    </div>
                    <button
                        onClick={() => setSidebarOpen(false)}
                        className={`lg:hidden p-2 rounded-md ${themeClasses.themeMutedFg} ${themeClasses.hover} transition-colors`}
                    >
                        <X className="h-5 w-5" />
                    </button>
                </div>

                {/* Navigation with theme support */}
                <nav className="flex-1 px-4 py-6 space-y-2 overflow-y-auto">
                    {navigation.map((item) => {
                        const Icon = item.icon;
                        const isActive = typeof window !== 'undefined' && window.location.pathname === item.href;

                        return (
                            <a
                                key={item.name}
                                href={item.href}
                                onClick={() => setSidebarOpen(false)}
                                className={`flex items-center px-3 py-2 ${themeClasses.textSm} font-medium rounded-lg transition-colors ${isActive
                                        ? 'accent-bg text-white'
                                        : `${themeClasses.themeMutedFg} ${themeClasses.hover} hover:${themeClasses.themeFg}`
                                    }`}
                            >
                                <Icon className={`h-5 w-5 mr-3 ${isActive ? 'text-white' : themeClasses.themeMutedFg}`} />
                                {item.name}
                            </a>
                        );
                    })}
                </nav>

                {/* Language Selector */}
                <div className={`border-t ${themeClasses.themeBorder} p-4 flex-shrink-0`}>
                    <div className="relative">
                        <button
                            onClick={() => setLanguageDropdownOpen(!languageDropdownOpen)}
                            className={`flex items-center w-full px-3 py-2 ${themeClasses.textSm} ${themeClasses.themeMutedFg} ${themeClasses.hover} hover:${themeClasses.themeFg} rounded-lg transition-colors`}
                        >
                            <Languages className="h-4 w-4 mr-2" />
                            {t('language')}
                        </button>

                        {languageDropdownOpen && (
                            <div className={`absolute bottom-full left-0 mb-2 w-full ${themeClasses.themeCard} rounded-lg shadow-lg border ${themeClasses.themeBorder} py-1 z-50`}>
                                {supportedLanguages.map((lang) => (
                                    <button
                                        key={lang.code}
                                        onClick={() => handleLanguageChange(lang.code)}
                                        className={`flex items-center w-full px-3 py-2 ${themeClasses.textSm} ${currentLanguage === lang.code
                                                ? `${themeClasses.themeFg} accent-bg accent-text`
                                                : `${themeClasses.themeMutedFg} ${themeClasses.hover} hover:${themeClasses.themeFg}`
                                            } transition-colors`}
                                    >
                                        <span className="mr-2">{lang.flag}</span>
                                        <span>{lang.nativeName}</span>
                                    </button>
                                ))}
                            </div>
                        )}
                    </div>
                </div>

                {/* User profile section with theme support */}
                <div className={`border-t ${themeClasses.themeBorder} p-4 flex-shrink-0`}>
                    <div className={`flex items-center ${isCompact ? 'space-x-2 mb-2' : 'space-x-3 mb-3'}`}>
                        <div className="flex-shrink-0">
                            <div className={`${isCompact ? 'h-8 w-8' : 'h-10 w-10'} rounded-full accent-bg flex items-center justify-center`} style={{ opacity: 0.1 }}>
                                <Users className={`${isCompact ? 'h-4 w-4' : 'h-5 w-5'} accent-text`} />
                            </div>
                        </div>
                        <div className="flex-1 min-w-0">
                            <p className={`${themeClasses.textSm} font-medium ${themeClasses.themeFg} truncate`}>
                                {user.firstName} {user.lastName}
                            </p>
                            <p className={`text-xs ${themeClasses.themeMutedFg} truncate`}>{user.email}</p>
                        </div>
                    </div>
                    <button
                        onClick={handleLogout}
                        className={`flex items-center w-full px-3 py-2 ${themeClasses.textSm} ${themeClasses.themeMutedFg} ${themeClasses.hover} hover:${themeClasses.themeFg} rounded-lg transition-colors`}
                    >
                        <LogOut className="h-4 w-4 mr-2" />
                        {t('logout')}
                    </button>
                </div>
            </div>

            {/* Main content area with theme support */}
            <div className="flex-1 flex flex-col overflow-hidden lg:ml-0">
                {/* Top bar with theme support */}
                <div className={`${themeClasses.themeCard} shadow-sm border-b ${themeClasses.themeBorder} flex-shrink-0 theme-transition`}>
                    <div className={`flex items-center justify-between ${isCompact ? 'h-12 px-4' : 'h-16 px-4 sm:px-6 lg:px-8'}`}>
                        <button
                            onClick={() => setSidebarOpen(true)}
                            className={`lg:hidden p-2 rounded-md ${themeClasses.themeMutedFg} ${themeClasses.hover} hover:${themeClasses.themeFg} transition-colors`}
                        >
                            <Menu className="h-5 w-5" />
                        </button>

                        <div className="flex items-center space-x-4">
                            <div className="hidden sm:block">
                                <p className={`${themeClasses.textSm} ${themeClasses.themeMutedFg}`}>
                                    {t('welcomeBack', { name: user.firstName })}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Page content with theme support */}
                <main className={`flex-1 overflow-y-auto ${themeClasses.themeBg} theme-transition`}>
                    {children}
                </main>
            </div>
        </div>
    );
};

export default OrganizerClientLayout;